@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Lato", sans-serif;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: cover;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #ffffff;
}

.main {
  flex-grow: 1;
}

.button {
  display: inline-block;
  padding: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:focus {
  background-color: #5eaa2f;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}

.button--light {
  color: #444444;
  background-color: #f2f2f2;
}
.button--light:hover {
  background-color: #ebebeb;
}
.button--light:focus {
  background-color: #ebebeb;
}
.button--light:active {
  background-color: #969090;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .container {
    padding: 0 60px;
  }
}

.page-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}
@media (min-width: 768px) {
  .page-title {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .page-title {
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
  }
}

.logo {
  display: block;
  width: fit-content;
  color: #111111;
  cursor: pointer;
  --color-logo-text: #111111;
  fill: currentColor;
}
.logo--full {
  display: grid;
  align-items: center;
  column-gap: 56px;
  grid-template-columns: auto 1fr;
}

.logo__cat-img {
  display: block;
  width: 33px;
  height: 38px;
}

.logo__cat-text {
  display: block;
  width: 101px;
  height: 18px;
}

.logo--footer:hover {
  color: #3e3e3e;
}
.logo--footer:focus {
  color: #3e3e3e;
}
.logo--footer:active {
  color: #6b6b6b;
}

.site-list {
  margin: 0;
  padding: 0;
}

.site-list__item {
  position: relative;
  text-align: center;
}

.site-list__link {
  display: block;
  padding: 23px 23px 22px;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  color: inherit;
  color: rgb(17, 17, 17);
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .site-list__link {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
  }
}
@media (min-width: 1200px) {
  .site-list__link {
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
  }
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:focus {
  opacity: 0.3;
}
.site-list__link:active::after {
  content: "";
  position: absolute;
  right: 12px;
  bottom: 8px;
  left: 22px;
  width: 168px;
  height: 2px;
  color: #000000;
  background-color: #68b738;
}

.nav__toggle {
  width: 24px;
  height: 24px;
  margin: 0;
  padding: 4px 0;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .nav__list {
    position: static;
    gap: 33px;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    display: flex;
    flex-direction: row;
  }
}

.main-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 13px 20px 14px;
}
@media (min-width: 768px) {
  .main-header__container {
    padding: 25px 30px 20px;
    grid-template-columns: auto, 1fr;
  }
}
@media (min-width: 1200px) {
  .main-header__container {
    padding: 55px 110px 20px 60px;
    grid-template-columns: 1fr, 1fr;
  }
}

.main-header__logo {
  display: flex;
  flex-wrap: wrap;
  max-height: 38px;
}

.main-header__navigation {
  margin: 0;
  padding: 0;
}

.main-header__toggle,
.nav__toggle {
  width: 18px;
  height: 18px;
  padding: 0;
  cursor: pointer;
}

@media (min-width: 1200px) {
  .promo__container {
    display: grid;
    padding: 0 0 0 60px;
    grid-template-columns: 1fr, 1fr;
  }
}

.promo__description {
  padding: 27px 35px 25px;
  color: #ffffff;
  background-color: rgba(104, 183, 56, 0.85);
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-mobile.jpg");
  background-repeat: repeat, no-repeat;
  background-position: top;
  background-size: auto, cover;
}
@media (min-width: 768px) {
  .promo__description {
    padding: 66px 90px 0;
    background: none;
  }
}
@media (min-width: 1200px) {
  .promo__description {
    padding: 110px 80px 30px;
    grid-column: 1/2;
  }
}

.promo__title {
  margin-bottom: 0 0 25px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: rgb(255, 255, 255);
}
@media (min-width: 768px) {
  .promo__title {
    margin-bottom: 40px;
    padding: 90;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}

.promo__text {
  margin: 0 0 25px;
  font-size: 14px;
  line-height: 14px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__text {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    text-transform: uppercase;
  }
}
@media (min-width: 1200px) {
  .promo__text {
    margin-bottom: 52px;
  }
}

.promo__button {
  width: 100%;
}

@media (min-width: 768px) {
  .promo__img {
    max-width: 100%;
    max-height: 609px;
  }
}
@media (min-width: 1200px) {
  .promo__img {
    background-color: rgba(104, 183, 56, 0.85);
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-mobile.jpg");
    background-repeat: repeat, no-repeat;
    background-position: top;
    background-size: auto, cover;
    grid-column: 2/3;
  }
}

.promo__img-can {
  width: 100%;
  height: 499px;
  object-fit: contain;
}

.offer__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 20px;
}
@media (min-width: 768px) {
  .offer__list {
    gap: 30px;
  }
}
@media (min-width: 1200px) {
  .offer__list {
    gap: 80px;
    grid-template-columns: 1fr 1fr;
  }
}

.offer__item {
  min-height: 266px;
  padding: 0 20px 33px 18px;
  text-transform: uppercase;
  background: rgb(242, 242, 242);
}
@media (min-width: 768px) {
  .offer__item {
    padding: 77px 63px 57px 52px;
  }
}
@media (min-width: 1200px) {
  .offer__item {
    padding: 77px 61px 59px 52px;
  }
}

.offer__title {
  margin: 0 0 29px 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #000000;
