}
@media (min-width: 768px) {
  .offer__text {
    max-width: 340px;
    margin: 0 0 48px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
  }
}
@media (min-width: 1200px) {
  .offer__text {
    margin: 0 0 27px 0;
  }
}

.offer__link {
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-decoration: none;
  cursor: pointer;
}
.offer__link:active {
