}

.technology__container {
  padding: 20px 20px 53px;
}
@media (min-width: 768px) {
  .technology__container {
    padding: 45px 109px 46px 30px;
  }
}
@media (min-width: 1200px) {
  .technology__container {
    padding: 71px 60px 70px;
  }
}

.technology__title {
  margin: 0 0 42px;
  padding: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}
@media (min-width: 768px) {
