    line-height: 60px;
  }
}

.technology__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 20px;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .technology__list {
    column-gap: 154px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: 37px;
  }
}
@media (min-width: 1200px) {
  .technology__list {
    gap: 78px;
    grid-template-columns: repeat(4, 1fr);
  }
}

.technology__item {
  position: relative;
  padding: 0 0 0 80px;
  font-size: 14px;
  line-height: 18px;
  color: rgb(68, 68, 68);
}
@media (min-width: 768px) {
  .technology__item {
    font-size: 16px;
    line-height: 24px;
    color: rgb(68, 68, 68);
  }
}

.slider {
  position: relative;
  width: 100%;
  max-width: 280px;
  margin: 0 auto;
}

.slider__container {
  position: relative;
  min-height: 256px;
  overflow: hidden;
}

.slider__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.slider__item img {
  display: block;
  width: 100%;
  height: 100%;
}

.slider__item--before {
  z-index: 2;
}

.slider__item--after {
  right: 0;
  left: auto;
  z-index: 1;
}

.slider__item--before img {
  object-position: left;
}

.slider__item--after img {
  object-position: right;
}

.example__title {
  margin: 0 0 40px;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
  color: #000000;
