    margin: 0 0 76px;
  }
}

.example__container {
  padding: 25px 20px;
  background: rgb(234, 234, 234);
}
@media (min-width: 768px) {
  .example__container {
    padding: 45px 20px 101px;
    background: rgb(255, 255, 255);
  }
}
@media (min-width: 1200px) {
  .example__container {
    display: grid;
    padding: 0 60px 75px;
    background: rgb(242, 242, 242);
    grid-template-columns: 436px 1fr;
  }
}

.example__result-description {
  display: block;
  margin: 8px 8px -29px;
  padding: 0 5px;
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  background-color: #eaeaea;
}

.example__text {
  margin: 0 0 20px 0;
  font-size: 14px;
  line-height: 18px;
  color: rgb(68, 68, 68);
}
@media (min-width: 768px) {
  .example__text {
    margin-bottom: 55px;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .example__text {
    margin-bottom: 69px;
    color: rgb(68, 68, 68);
  }
}

@media (min-width: 768px) {
  .example__result {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1200px) {
  .example__result {
