  text-transform: uppercase;
}

.application__container {
  width: 100%;
  max-width: 1260px;
  padding: 27px 20px 18px;
}

.application__title {
  margin: 0 0 37px;
  padding: 0;
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", sans-serif;
}

.application__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.control {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 12px;
  grid-template-columns: 1fr;
}

.control__item {
  display: grid;
  align-items: center;
  min-height: 30px;
  grid-template-columns: auto 1fr;
}

.control__label {
  padding-left: 15px;
}

.control__input {
  position: relative;
  order: -1;
  width: 30px;
  height: 30px;
  margin: 0;
  border: 1px solid #cdcdcd;
  appearance: none;
}

.control__input--radio {
  margin-right: 10px;
  border-radius: 50%;
}
.control__input--radio:checked::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.control__input--check:checked {
  background-image: url("../../img/svg/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 26px 26px;
}

.form__container {
  max-width: 1440px;
  padding: 0 60px;
}

.form__group-wrapper {
  display: grid;
  gap: none;
}
@media (min-width: 1200px) {
  .form__group-wrapper {
